.testimonial-quote {
  max-width: 300px;
  /* Even though the styles below are only used when the rating widget is
  set to show up, they are located here to make use of the cleaner &__ syntax.
  If we decide it's better to include them inside the
  &.testimonial-quote_with-rating rule below,
  we will need to make the &__ replacements.
  */
}
@media (min-width: 768px) {
  .testimonial-quote {
    max-width: 736px;
  }
}
@media (min-width: 1024px) {
  .testimonial-quote {
    max-width: 820px;
  }
}
.testimonial-quote__inner {
  margin-bottom: 50px;
}
@media (min-width: 1024px) {
  .testimonial-quote__inner {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .testimonial-quote__text {
    flex-basis: 86%;
    padding-right: 75px;
  }
}
@media (min-width: 1280px) {
  .testimonial-quote__text {
    flex-basis: 66%;
  }
}
.testimonial-quote__quote_sm {
  font-size: 1rem;
  line-height: calc( 31 / 16 );
}
@media (min-width: 768px) {
  .testimonial-quote__quote_sm {
    font-size: 1.1875rem;
    line-height: calc( 37 / 20 );
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__quote_sm {
    font-size: 1.375rem;
    line-height: calc( 43 / 29 );
  }
}
.testimonial-quote__quote_md {
  font-size: 1.125rem;
  line-height: calc( 31 / 18 );
}
@media (min-width: 768px) {
  .testimonial-quote__quote_md {
    font-size: 1.375rem;
    line-height: calc( 37 / 22 );
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__quote_md {
    font-size: 1.5625rem;
    line-height: calc( 43 / 25 );
  }
}
.testimonial-quote__quote_lg {
  font-size: 1.3125rem;
  line-height: calc( 31 / 21 );
}
@media (min-width: 768px) {
  .testimonial-quote__quote_lg {
    font-size: 1.5625rem;
    line-height: calc( 37 / 25 );
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__quote_lg {
    font-size: 1.8125rem;
    line-height: calc( 43 / 29 );
  }
}
.testimonial-quote__quote::before {
  position: absolute;
  top: -30px;
  left: -10px;
  z-index: -1;
  width: 58px;
  height: 48px;
  background: url(./../../../../assets/images/testimonial-quote/quote.svg) no-repeat center center;
  background-size: 58px 48px;
  content: "";
}
.testimonial-quote__author-name, .testimonial-quote__author-description, .testimonial-quote__author-location {
  font-weight: 500;
  font-size: 0.8125rem;
  line-height: calc( 16 / 16 );
}
@media (min-width: 1024px) {
  .testimonial-quote__author-name, .testimonial-quote__author-description, .testimonial-quote__author-location {
    font-size: 0.9375rem;
    line-height: calc( 18 / 16 );
  }
}
.testimonial-quote__attribution-text {
  flex-basis: 84%;
}
.testimonial-quote__image-container {
  max-width: 280px;
  height: auto;
  padding-top: 7px;
}
.testimonial-quote__image-container_small {
  flex-basis: 16%;
}
.testimonial-quote__image-container_large {
  flex-basis: 33%;
}
.testimonial-quote__image-container img {
  width: 100%;
  height: auto;
}
.testimonial-quote__getapp-reviews {
  border-top: 1px solid #ced5da;
  border-bottom: 1px solid #ced5da;
}
@media (min-width: 1024px) {
  .testimonial-quote__getapp-reviews {
    flex-direction: column !important;
    padding-left: 60px;
    border-top: 0;
    border-bottom: 0;
  }
}
.testimonial-quote__getapp-reviews .testimonial-quote__getapp-reviews-title {
  color: #0075dd;
  font-size: 1.75rem;
  line-height: calc( 36 / 30 );
}
@media (min-width: 768px) {
  .testimonial-quote__getapp-reviews .testimonial-quote__getapp-reviews-title {
    max-width: 350px;
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__getapp-reviews .testimonial-quote__getapp-reviews-title {
    max-width: none;
  }
}
@media (min-width: 1280px) {
  .testimonial-quote__getapp-reviews .testimonial-quote__getapp-reviews-title {
    font-size: 2.125rem;
    line-height: calc( 43 / 36 );
  }
}
@media (min-width: 768px) {
  .testimonial-quote__getapp-reviews-widget {
    margin-left: 20px;
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__getapp-reviews-widget {
    margin-left: 0;
  }
}
.testimonial-quote__getapp-reviews .rating {
  justify-content: center;
}
.testimonial-quote__getapp-reviews .rating.rating__wrap_1 {
  flex-direction: column;
}
.testimonial-quote__getapp-reviews .rating__top {
  margin-right: 0 !important;
}
.testimonial-quote__getapp-reviews .rating__category {
  font-size: 1.3125rem;
  line-height: calc( 25 / 21 );
}
@media (min-width: 768px) {
  .testimonial-quote__getapp-reviews .rating__category {
    font-size: 1.5625rem;
    line-height: calc( 31 / 25 );
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__getapp-reviews .rating__category {
    font-size: 1.3125rem;
    line-height: calc( 25 / 21 );
  }
}
@media (min-width: 1280px) {
  .testimonial-quote__getapp-reviews .rating__category {
    font-size: 1.5625rem;
    line-height: calc( 30 / 25 );
  }
}
@media (min-width: 768px) {
  .testimonial-quote__getapp-reviews .rating__description, .testimonial-quote__getapp-reviews .rating__location {
    font-size: 1.0625rem;
    line-height: calc( 21 / 17 );
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__getapp-reviews .rating__description, .testimonial-quote__getapp-reviews .rating__location {
    font-size: 0.875rem;
    line-height: calc( 18 / 14 );
  }
}
@media (min-width: 1280px) {
  .testimonial-quote__getapp-reviews .rating__description, .testimonial-quote__getapp-reviews .rating__location {
    font-size: 1.0625rem;
    line-height: calc( 20 / 17 );
  }
}
@media (min-width: 768px) {
  .testimonial-quote__getapp-reviews .rating__star-image {
    width: 162px;
    height: auto;
  }
}
@media (min-width: 1024px) {
  .testimonial-quote__getapp-reviews .rating__star-image {
    width: 134px;
  }
}
@media (min-width: 1280px) {
  .testimonial-quote__getapp-reviews .rating__star-image {
    width: 161px;
  }
}
.testimonial-quote.testimonial-quote_with-rating {
  max-width: 1034px;
}
@media (min-width: 1280px) {
  .testimonial-quote.testimonial-quote_with-rating {
    max-width: 1250px;
  }
}
@media (min-width: 1024px) {
  .testimonial-quote.testimonial-quote_with-rating .testimonial-quote__inner {
    justify-content: center;
    max-width: 598px;
    padding-right: 60px;
    border-right: 1px solid #ced5da;
  }
}
@media (min-width: 1280px) {
  .testimonial-quote.testimonial-quote_with-rating .testimonial-quote__inner {
    max-width: 783px;
  }
}
@media (min-width: 1024px) {
  .testimonial-quote.testimonial-quote_with-rating .testimonial-quote__author-name,
.testimonial-quote.testimonial-quote_with-rating .testimonial-quote__author-description,
.testimonial-quote.testimonial-quote_with-rating .testimonial-quote__author-location {
    font-size: 0.6875rem;
    line-height: calc( 13 / 11 );
  }
}
@media (min-width: 1280px) {
  .testimonial-quote.testimonial-quote_with-rating .testimonial-quote__author-name,
.testimonial-quote.testimonial-quote_with-rating .testimonial-quote__author-description,
.testimonial-quote.testimonial-quote_with-rating .testimonial-quote__author-location {
    font-size: 0.8125rem;
    line-height: calc( 16 / 13 );
  }
}